.comments {
  margin-top: 20px;
  margin-left: 15px;
}

.comments-title {
  font-size: 30px;
  margin-bottom: 20px;
}

.comments-container {
  margin-top: 40px;
}

.comment-form-title {
  font-size: 22px;
}

.comment-form-textarea {
  width: 400px;
  height: 80px;
  margin-bottom: 8px;
  margin-top: 8px;
  margin-right: 20px;
  border: 1px solid rgb(107, 114, 12);
  resize: none;
}

@media screen and (max-width: 450px) {
    .comment-form-textarea {
      width: 300px;
    }
  }

.comment-form-button {
  font-size: 16px;
  padding: 8px 16px;
  background: #4d4d4d;
  border-radius: 8px;
  color: white;
}

.comment-form-button:hover:enabled {
  cursor: pointer;
  background: teal;
}

.comment-form-button:disabled {
  opacity: 0.5;
  cursor: default;
}

.comment-form-cancel-button {
  margin-left: 10px;
}

.comment {
  display: flex;
  margin-bottom: 28px;
}

.comment-image-container {
  margin-right: 12px;
}

.comment-image-container img {
  border-radius: 50px;
}

.comment-right-part {
  width: 100%;
}

@media screen and (max-width: 450px) {
    .comment-right-part {
      margin-right: 7px;
    }
  }

.comment-content {
  display: flex;
  color: gray;
}

.comment-author {
  margin-right: 8px;
  font-size: 20px;
  color: rgb(59, 130, 246);
  color: teal;
}

.comment-text {
  font-size: 18px;
}

.comment-actions {
  display: flex;
  font-size: 12px;
  color: rgb(51, 51, 51);
  cursor: pointer;
  margin-top: 8px;
}

.comment-action {
  margin-right: 8px;
}

.comment-action:hover {
  text-decoration: underline;
}

.replies {
  margin-top: 20px;
}